import lan from '../../static/js/language'
import Head from './head'
import Loading from './Loading'
  export default {
      name: "Validation",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          data:{
            token: localStorage.getItem('token')
          },
          stepNum: '',
          isStep:'1',
          options:[
            {label:'中国',value:'0'}
          ],
          cuntry:'',
          radio:"",
          imageUrl1:'',
          imageUrl:'',
          firstId:'',
          secendId:'',
          huzhaoId:'',
          firstlanguage:'',
          language:'',
          languageList:[],
          introduceText:'',
          isVolunteer:false,
          audit_idcard:'',
          isSeccond:false,
          isFirst: false,
          addStep:'',
          isWait:false
        }
    },
    components:{
      Head,
      Loading,
    },
    methods:{
      changeStep(val){
        if(val == 1){
          this.stepNum += this.addStep
          this.isStep = 2
        }else if(val == 2){
          this.stepNum += this.addStep
          this.isStep = 3
        }else if(val == 3){
          if(!this.cuntry.length){
            this.$message({
              type: 'error',
              message: '请选择国家!'
            })
          }else if(!this.radio){
            this.$message({
              type: 'error',
              message: '您还没有选择类型'
            })
          }else if(this.radio == '1'){
            this.stepNum += this.addStep
            this.isStep = 4
          }else{
            this.stepNum += this.addStep
            this.isStep = 5
          }
        }else if(val == 4){
          if(this.firstId && this.secendId){
            this.checkSfz()
          }else{
            this.$message({
              type:'info',
              message:'您的图片还没上传完整！'
            })
          }
        }else if(val == 5){
          if(!this.huzhaoId){
            this.$message({
              type: 'info',
              message: '请上传您的护照正面'
            })
          }else{
            this.checkHz()
          }
        }else if(val == 6 || val == 8){
          this.$router.push('/')
        }
      },
      goBack(val){
        if(val == 5){
          this.isStep = 3
          this.stepNum = this.stepNum - (2*this.addStep)
        }else if(val == 1){
          this.isStep = 'volunteer'
          this.stepNum -= this.addStep
        }else{
          this.isStep -= 1
          this.stepNum -= this.addStep
        }
      },
      firstHandleAvatarSuccess(res,file){
        if(res.code == 1){
          this.imageUrl1 = res.data.domain + res.data.image_url
          this.firstId = res.data.image_id
        }else{
          alert('上传失败！请重新上传')
        }
      },
      handleAvatarSuccess(res, file) {
        if(res.code == 1){
          this.imageUrl = res.data.domain + res.data.image_url
          this.secendId = res.data.image_id
        }else{
          alert('上传失败！请重新上传')
        }
      },
      huzhaoSuccess(res){
        if(res.code == 1){
          this.imageUrl = res.data.domain + res.data.image_url
          this.huzhaoId = res.data.image_id
        }else{
          alert('上传失败！请重新上传')
        }
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg'|| file.type === 'image/png' || file.type === 'image/jpg';
        const isLt10M = file.size / 1024 / 1024 < 10;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG/JPEG/PNG 格式!');
          this.isFirst = false
          this.isSeccond = false
        }
        if (!isLt10M) {
          this.$message.error('上传头像图片大小不能超过 10MB!');
          this.isFirst = false
          this.isSeccond = false
        }
        return isJPG && isLt10M;
      },
      checkSfz(){
        this.isWait = true
        this.$http.post(this.api + '/IdentifyCard',{
          token: localStorage.getItem('token'),
          idcard_z: this.firstId,
          idcard_f: this.secendId,
          flag:1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.stepNum = 100
              this.isStep = 6
              this.isWait = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.checkSfz()
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isWait = false
            }
          })
      },
      checkHz(){
        this.isWait = true
        this.$http.post(this.api + '/IdentifyCard',{
          token: localStorage.getItem('token'),
          passport: this.huzhaoId,
          flag:2
        })
          .then(res=>{
            if(res.data.code == 1){
              this.stepNum = 100
              this.isStep = 6
              this.isWait = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.checkSfz()
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isWait = false
            }
          })
      },
      goBackHome(){
        this.$router.go(-1)
      },
      nextStep(){
        if(this.firstlanguage !== '' && this.introduceText && this.language.length){
          this.beComeVolun()
        }else{
          this.$message({
            type: 'error',
            message: '您还没有填写完整!'
          })
        }
      },
      beComeVolun(){
        this.$http.post(this.api + '/VolunteerB',{
          token: localStorage.getItem('token'),
          other_language: JSON.stringify(this.language),
          main_language: this.firstlanguage,
          introduce: this.introduceText,
        })
          .then(res=>{
            if(res.data.code == 1){
              if(this.audit_idcard){
                this.isStep = 8
                this.stepNum = 100
              }else{
                this.isStep = 1
                this.stepNum += this.addStep
                this.$message({
                  type: 'success',
                  message:'已提交，请继续完成身份验证！'
                })
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.beComeVolun()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      reload(res){
        let data = res.data.data[0]
        this.audit_idcard = data.audit_idcard
        if(this.$route.query.information){
          if(this.audit_idcard){
            this.isStep = this.$route.query.information
            this.stepNum = 50
            this.isVolunteer = true
          }else{
            this.isStep = this.$route.query.information
            this.addStep = 50/3
            this.stepNum = 50/3
            this.isVolunteer = true
          }
        }else{
          this.isStep = '1'
          this.addStep = 20
          this.stepNum = 20
        }
      },
      uploadLoading(file,fileList){
        if(file.status == 'ready'){
          this.isFirst = true
        }else if(file.status == 'success'){
          this.isFirst = false
        }
      },
      uploadScecond(file,fileList){
        console.log(file)
        if(file.status == 'ready'){
          this.isSeccond = true
        }else if(file.status == 'success'){
          this.isSeccond = false
        }
      },
    },
    created(){
      this.languageList = lan()
    }
  }



